*
{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body
{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    line-height: 1.4;
    columns: #1f1f1f;
    background-color: black;
}
.container{
    max-width: 700px;
    margin: 0 auto;
    padding: 0 10px;
    background-color:steelblue ;
    margin-top: 1cm;
    border: 3px solid springgreen;
}
.form-container
{
    display: flex;
    width: 100%;
}
.input-text{
    flex: 8;
    font-size: 14px;
    padding: 12px 15px;
    border: 1px solid springgreen;
    outline: none;
    font-weight: 400;
    max-width: 80%;
}
.input-text::placeholder{
    color: rgb(50, 17, 17);
    opacity: 0.8;
}
.input-submit{
    flex: 2;
    border: none;
    background: blueviolet;
    color: chartreuse;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: 600;
    width: 20%;
    outline: none;
}
.todo-item{
    list-style-type: none;
    padding: 17px 0px;
    border-bottom: 1px solid #eaeaea;
}
.todo-item input{
    margin-right: 15px;
}
.todo-item button{
    background: #dd5544;
    color: #fff;
    border: 1px solid #dd5544;
    padding: 3px 7px;
    cursor: pointer;
    float: right;
    outline: none;
}
